/**
 * Styles for the block component.
 */
.block {
	$block: &;
	background-color: $c-creme;
	text-align: center;
	overflow: hidden;

	@include media-breakpoint-up(lg) {
		display: flex;
	}

	// Selector styles
	// TODO: add code

	// @media queries
	// TODO: add code

	// Pseudo-classes and pseudo-elements
	// TODO: add code

	// Nested selectors
	// TODO: add code

	// BEM elements
	// TODO: add code
	&__img {
		display: block;
		object-fit: cover;
		object-position: center;
		width: 100%;
		height: 100%;
	}

	&__picture {
		// height: 490px;

		@include media-breakpoint-up(lg) {
			// height: 630px;
			flex: 1;
		}
	}

	&__content {
		padding: 82px 0;
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		&--product {
			padding: 42px 0 54px;
		}

		&--gallery {
			padding-top: 52px;

			@include media-breakpoint-up(lg) {
				padding-top: 150px;
				padding-bottom: 162px;
			}
		}

		&--ingredients {
			padding-bottom: 42px;
		}
	}

	&__subtitle {
		color: $c-gold;
		margin-bottom: $gutter / 3;

		@include media-breakpoint-up(lg) {
			margin-bottom: 6px;
			// TODO: remove
			margin-bottom: 11px;
		}
	}

	&__title {
		color: $c-brown;
		margin-bottom: 26px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 23px;
		}
	}

	&__text {
		color: $c-lbrown;
		font-size: $fs-18body;
		line-height: 29px;
		margin-bottom: $gutter;

		@include media-breakpoint-up(lg) {
			max-width: 500px;
			margin: 0 auto 39px;
		}
	}

	&__btn {
		display: flex;
		justify-content: center;
	}

	&__price {
		color: $c-gold;
		font-size: $fs-lbody;
		line-height: $lh-default;
		margin-top: -8px;
		margin-bottom: 16px;

		@include media-breakpoint-up(lg) {
			font-size: $fs-h2;
			line-height: $lh-h2;
			margin-bottom: 20px;
			margin-top: -1px;
		}
	}

	&__action {
		text-decoration: none;
		font-size: $fs-default;
		line-height: $lh-default;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		margin-top: 25px;
		color: #A6917A;
		display: inline-block;
	}

	&__gallery {
		padding-top: 60px;
		flex: 1;

		@include media-breakpoint-up(lg) {
			width: 50%;
			padding-top: 130px;

			.container {
				padding: 0;
				display: flex;
			}
		}
	}

	&__thumbs_container {
		display: none;
		width: 92px;
		flex-shrink: 0;
		margin-right: 20px;

		@include media-breakpoint-up(lg) {
			display: block;
		}
	}

	&__product_container {
		overflow: visible;
		
		@include media-breakpoint-up(lg) {
			flex: 1 1 auto;
			width: 0;
			overflow: hidden;
		}
	}

	&__product_wrapper {
		padding-bottom: 28px;
	}

	&__atributes {
		font-size: $fs-18body;
		line-height: $lh-default;
		color: $c-brown;
		margin-top: -1px;
    	margin-bottom: 13px;

		@include media-breakpoint-up(lg) {
			margin-top: 0;
			margin-bottom: 1px;
		}
	}

	&__counter {
		margin-top: -7px;
		margin-bottom: 41px;

		@include media-breakpoint-up(lg) {
			margin-top: -17px;
			margin-bottom: 20px;
		}
	}

	&__ingredients_picture {
		display: block;
		position: absolute;
		
		&:nth-child(1) {
			width: 152px;
			height: 125px;
			right: 34px;
			top: 106px;
			box-shadow: 0px 5.79683px 38.9968px rgba(82, 60, 51, 0.4);
			z-index: 20;
		}
		
		&:nth-child(2) {
			width: 101px;
			height: 122px;
			left: 99px;
			top: 146px;
			box-shadow: 0px 5.79683px 21.0794px rgba(82, 60, 51, 0.2);
			z-index: 19;
		}
		
		&:nth-child(3) {
			width: 202px;
			height: 182px;
			left: 34px;
			top: 0;
			z-index: 18;
		}
		
		&:nth-child(4) {
			width: 130px;
			height: 109px;
			right: 52px;
			top: 28px;
			box-shadow: 0px 2.10794px 2.10794px rgba(0, 0, 0, 0.25);
			z-index: 17;
		}
	}

	&__ingredients {
		position: relative;
		min-height: 380px;

		&--count3, &--count5 {
			margin-top: -15px;
			min-height: 395px;
		}
	}

	&__ingredients--count3 &__ingredients_picture {

		&:nth-child(1) {
			width: 156px;
			height: 179px;
			left: -3px;
			top: 96px;
		}

		&:nth-child(2) {
			width: 205px;
			height: 203px;
			left: 47px;
			top: -13px;
		}

		&:nth-child(3) {
			width: 302px;
			height: 272px;
			left: 75px;
			top: 46px;
		}
	}

	&__ingredients--count5 &__ingredients_picture {

		&:nth-child(1) {
			width: 105px;
			height: 89px;
			right: -3px;
			top: 84px;
		}

		&:nth-child(2) {
			width: 109px;
			height: 85px;
			left: -3px;
			top: 169px;
		}

		&:nth-child(3) {
			width: 115px;
			height: 111px;
			left: 52px;
			top: 205px;
		}

		&:nth-child(4) {
			width: 132px;
			height: 111px;
			right: 23px;
			top: 16px;
		}

		&:nth-child(5) {
			width: 269px;
			height: 242px;
			left: 51px;
			top: 42px;
		}
	}

	&__ingredients_img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}

	// BEM modifiers
	// TODO: add code
	&--reverse {
		flex-direction: row-reverse;
	}

	&--reverse_mobile {

		& > .container {
			display: flex;
			flex-direction: column-reverse;
	
			@include media-breakpoint-up(lg) {
				flex-direction: row-reverse;
			}
		}
	}
}
