	&__btn {
		text-align: center;
	}

	&__title {
		margin-bottom: 52px;

		@include media-breakpoint-up(xl) {
			margin-bottom: 50px;
		}
	}

	&__container {
		margin-bottom: 32px;
		overflow: visible;

		@include media-breakpoint-up(xl) {
			overflow: hidden;
			margin-bottom: 60px;
		}
	}

	&__wrapper {
		max-width: 280px;
		padding-bottom: 20px;

		@include media-breakpoint-up(lg) {
			max-width: none;
		}
	}

	&__navigation {
		display: none;
		width: 32px;
		height: 32px;
		background-color: transparentize($color: $c-white, $amount: 0.6);
		position: absolute;
		top: 164px;
		z-index: 10;
		align-items: center;
		justify-content: center;

		&--next {
			right: 20px;
		}

		&--prev {
			left: 20px;
		}

		@include media-breakpoint-up(lg) {
			display: block;
		}
	}

	&__icon {
		width: 24px;
		height: 24px;
	}

