	&__row {
		display: flex;
	}

	&__controls {
		position: relative;
		flex: 1;
		width: 100%;

		&--subscribe {
			flex: 0;
			width: auto;
			margin-left: 10px;
		}
	}

	&__label {
		font-size: $fs-14body;
		line-height: $lh-default;
		color: $c-dcreme;
		display: inline-block;
		margin-bottom: $gutter / 6;

		@include media-breakpoint-up(lg) {
			margin-bottom: 2px;
		}
	}

	&__input {
		@include reset-input();
		border-color: $c-dcreme;
		min-height: 54px;
		width: 100%;

		@include media-breakpoint-up(lg) {
			min-height: 40px;
		}

		&:focus {
			border-color: $c-dcreme;
		}
	}

